<template>
    <div id="login">
        <label for="">로그인</label>
       <form id="frm" name="frm" action="#" method="post" onsubmit="return false;">
            <input type="text" id="userId" name="userId" class="info" placeholder="아이디"><br/>
            <input type="password" id="userPw" name="userPw" class="info" placeholder="비밀번호"><br/>
            <input id="sb" v-on:click="test" type="submit" value="전송" class="btn">
       </form>
    </div>

</template>


<script>
    export default {
        name:'LoginComponent',
        methods:{
            test:function(){
                let userId = document.getElementById('userId').value;
                let userPw = document.getElementById('userPw').value;
                alert('id: '+userId+'/ pw: '+userPw);
            }
        }
    }

</script>

<style>
#login{
    border: 1px solid black;
    width: 400px;
    height: 300px;
}
.info{
    width: 300px;
    height: 30px;
    margin: 5px;
}
.btn{
    width:315px;
    height: 30px;
}
</style>